<template>
	<div>
		<header>
			<HeaderComponent :view-title="homeTitle"></HeaderComponent>
			<div>
				<ul class="user-info">
					<li class="user-info-img">
						<img class="user-img" v-bind:src="userImgUrl">
					</li>
					<li class="user-info-name">{{userName}}</li>
					<li class="user-info-vip"><span>{{userVip}}</span></li>
				</ul>
			</div>
		</header>
		<div class="home-menu">
			<div class="home-menu-top clear">
				<ul class="top-container">
					<li class="left">
						<router-link to="/activity">
							<svg class="icon" aria-hidden="true">
	  							<use xlink:href="#icon-chuangjian-red"></use>
							</svg>
							<p>创建活动</p>
						</router-link>
					</li>
					<li class="left">
						<router-link to="/vip">
							<svg class="icon" aria-hidden="true">
	  							<use xlink:href="#icon-huiyuan4"></use>
							</svg>
							<p>升级会员</p>
						</router-link>
					</li>
					<li class="top-container-line">|</li>
				</ul>
			</div>
			<div class="home-menu-bottom clear">
				<ul class="bottom-container clear">
					<li class="left">
						<router-link to="/activityTemplate">
							<svg class="icon" aria-hidden="true">
	  							<use xlink:href="#icon-moban"></use>
							</svg>
							<p>活动模板</p>
						</router-link>
					</li>
					<li class="left">
						<router-link to="/myActivity">
							<svg class="icon" aria-hidden="true">
	  							<use xlink:href="#icon-huodong"></use>
							</svg>
							<p>我的活动</p>
						</router-link>
					</li>
					<li class="left">
						<router-link to="/lottery">
							<svg class="icon" aria-hidden="true">
	  							<use xlink:href="#icon-icon_list_pressx"></use>
							</svg>
							<p>抽奖记录</p>
						</router-link>
					</li>
					<li class="left">
						<router-link to="/awardOff">
							<svg class="icon" aria-hidden="true">
	  							<use xlink:href="#icon-jiangpin"></use>
							</svg>
							<p>奖品核销</p>
						</router-link>
					</li>
					<li class="left">
						<router-link to="/dataStatistics">
							<svg class="icon" aria-hidden="true">
	  							<use xlink:href="#icon-shujutongji"></use>
							</svg>
							<p>数据统计</p>
						</router-link>
					</li>
					<li class="left">
						<router-link to="/setting">
							<svg class="icon" aria-hidden="true">
	  							<use xlink:href="#icon-shezhi-red-copy"></use>
							</svg>
							<p>账户设置</p>
						</router-link>
					</li>
					<li class="left">
						<router-link to="/help">
							<svg class="icon" aria-hidden="true">
	  							<use xlink:href="#icon-bangzhu"></use>
							</svg>
							<p>帮助中心</p>
						</router-link>
					</li>
					<li class="left">
						<router-link to="/customer">
							<svg class="icon" aria-hidden="true">
	  							<use xlink:href="#icon-kefu"></use>
							</svg>
							<p>联系客服</p>
						</router-link>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script type="text/javascript">
	import HeaderComponent from '../../components/header/header';
	export default {
		homeTitle:Object,
		userImgUrl:String,	//用户图片路径
		userName:String,	//用户名
		userVip:String,		//用户VIP级别
		data(){
			return {
				homeTitle:{
					name:'活动乐',
					showCode:0		//0 head不显示箭头，1 head显示箭头
				},
				userImgUrl:require('../../assets/images/user.jpg'),//src文件夹里面的assets文件夹里面的图片路径要通过require引入
				//userImgUrl:'../../../static/images/user.jpg',//根src文件夹同级的不需要static文件夹不用
				userName:'后海的大鱼鱼',
				userVip:'普通用户'
			}
		},
		components:{
			HeaderComponent
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/mixin';
	header{
		//@include wh(100%,2.5rem);
		//@include sc(1.5rem,#000);
		width:100%;
		//line-height: 2.5rem;
		text-align: center;	
	}
	.user-info{
		//@include wh(100%,5rem);
		width:100%;
		background: #F35353;
    	text-align: center;
    	margin-top:2.2rem;
    	&:before{
    		content:"";
    		display:table;
    	}
    	li.user-info-img{
    		margin-top:1rem;
    		margin-bottom:0.3rem;
    		img.user-img{
    			width:3rem;
    			@include borderRadius(50%);
    		}
    	}
    	li.user-info-name{
    		font-size:0.8rem;
   			margin-bottom:0.1rem;
   			color:#fff;
    	}
    	li.user-info-vip{
    		font-size:0.7rem;
    		span{
			    display: inline-block;
    			width: 3.5rem;
			    padding: 0.05rem 0;
			    border: 1px solid #fff;
			    @include borderRadius(1rem);
			    margin-bottom:2rem;
			    color:#fff;
    		}
    	}
    	li:before{
    		content:"";
    		display:table;
    	}
	}
	.home-menu{
		width:94%;
		margin:0 auto;
		margin-top:-1rem;
		//@include cl;	//该三项是使高度填满剩余高度空间
		//top:10rem;
		//bottom:0;
		&:before{
			content:"";
			display: table;
		}
		.home-menu-top{
			@include borderRadius(0.3rem);
			margin-bottom:0.5rem;
			text-align: center;
			background:#fff;
			&:before{
			content:"";
			display: table;
			}
			ul{
				li.left{
					width:50%;
					padding:0.5rem 0;
					p{
						font-size:0.7rem;
					}
				}
				li.top-container-line{
					@include cl;
					padding:0.5rem 0;
					font-size:1.3rem;
					color:#DCDCDC;
				}
			}
		}
		.home-menu-bottom{
			@include borderRadius(0.3rem);
			text-align: center;
			background:#fff;
			height:100%;
			ul{
				li{
					width:33.3333333333333333333%;
					padding:1rem 0;
					p{
						font-size:0.7rem;
					}
				}
			}
		}
	}
	.icon {
	   width: 1.4rem; height: 1.4rem;
	   vertical-align: -0.15em;
	   fill: currentColor;
	   overflow: hidden;
	   color:#F35353;
	}
</style>


